$bgcolor : #202124;
$textcolor : #e8eaed;
$disablecolor : #9aa0a6;
$bordercolor : #5f6368;

*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  color: $textcolor;
  outline: none;
  border: none; 
}

body{
  background: $bgcolor;
  padding: 20px;
  height: 100vh;
  position: relative;
}

.App{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
}
.Title{
  margin-bottom: 20px;
}

.wrap-list{
  width: 100%;
  border: 1px solid $bordercolor;
  border-radius: 25px;
  padding: 20px;
  min-height: 60vh;
}

.confirmed-disable{
  display: none;
  visibility: hidden;
  position: fixed;
  height: 50px;
  width: 200px;
  top: 50%;
  left: 50%;
  justify-content: space-around;
  transform: translate(-50%, -50%);
}
.confirmed-active{
  z-index: 999;
  display: flex;
  visibility: visible;
  border: 2px solid $bordercolor;
  border-radius: 25px;
  background:  rgba(51,51,51,0.9);
  position: fixed;
  min-width: 300px;
  top: 50%;
  left: 50%;
  padding: 20px;
  align-content: center;
  justify-content: space-around;
  transform: translate(-50%, -50%);
}


.InputDescription{
  margin-bottom: 20px;
  display: flex;
  min-height: 30px;
  height: auto;
  border: 1px solid $bordercolor;
  border-radius: 25px;
  padding: 20px;
  justify-content: space-between;
}


.text-input{
  overflow-y: visible;
  display: block;
  width: 80%;
  min-height: 50px;
  height: auto;
  background: none;
  font-size: 18px;
  margin-right: 5%;
  resize: vertical;
}

button{
  display: block;
  width: 70px;
  height: 40px;
  text-align: center;
  font-size: 18px;
  cursor: pointer;
  background: none;
  border: 2px solid $bordercolor;
  border-radius: 25px;
}
button:hover{
  box-shadow: 0px 0px 0px 10px rgba(51,51,51,0.7);
  background: rgba(51,51,51,0.7);
}


.todo-item{
  min-height: 30px;
  display: flex;
  margin-bottom: 20px;
}

.wrap-input{
  position: relative;
  min-width: 5%;
  padding-top: 5px;
  margin-right: 20px;
  .checkmark{
    position: absolute;
    width: 16px;
    height: 16px;
    border: 1px solid $bordercolor;
  }
  .disable:after{
    content: "";
    display: none;
    position: absolute;
    height: 10px;
    width: 4px;
    left: 4px;
    bottom: 2px;
    border-right: 3px solid $bordercolor;
    border-bottom: 3px solid $bordercolor;
    transform: rotate(45deg);
  }
  .active:after{
    content: "";
    display: block;
    position: absolute;
    height: 10px;
    width: 4px;
    left: 4px;
    bottom: 2px;
    border-right: 3px solid $bordercolor;
    border-bottom: 3px solid $bordercolor;
    transform: rotate(45deg);
  }
  .input-checkbox{
    opacity: 0;
    position: absolute; 
    cursor: pointer;
    width: 16px;
    height: 16px;
  }
}


.wrap-description{
  width: 90%;
  .description{
      font-size: 18px;
  }
  .active{
      color: $textcolor;
      text-decoration: none;
  }
  .disable{
      color: $disablecolor;
      text-decoration: line-through;
  }
}

.wrap-delete{
  display: block;
  height: 20px;
  width: 20px;
  padding-top: 5px;
  margin-left: 20px;
  .close{
      cursor: pointer;
      position: relative;
      width: 20px;
      height: 20px;
      background: none;
      border: none;
  }
  .close:before, .close:after {
      position: absolute;
      content: ' ';
      top: 0;
      height: 20px;
      width: 2px;
      background-color: #333;
  }
  .close:before {
  transform: rotate(45deg);
  }
  .close:after {
  transform: rotate(-45deg);
  }
}